<template>
  <div class="txt-to-pdf">
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <span>TXT to PDF</span>
          <el-icon><Document /></el-icon>
        </div>
      </template>

      <el-form :model="form" label-width="120px">
        <el-form-item label="Select TXT File" required>
          <el-upload
            ref="uploadRef"
            :auto-upload="false"
            :limit="1"
            :on-change="handleFileChange"
            :before-upload="beforeUpload"
            accept=".txt"
            drag
          >
            <el-icon class="el-icon--upload"><upload-filled /></el-icon>
            <div class="el-upload__text">Drag TXT file here, or <em>click to upload</em></div>
            <template #tip>
              <div class="el-upload__tip">Only .txt files allowed, max 100MB</div>
            </template>
          </el-upload>
        </el-form-item>

        <el-form-item label="Text Encoding">
          <el-select v-model="form.encoding">
            <el-option label="UTF-8" value="utf-8" />
            <el-option label="GBK" value="gbk" />
            <el-option label="GB2312" value="gb2312" />
            <el-option label="ISO-8859-1" value="iso-8859-1" />
          </el-select>
        </el-form-item>

        <el-form-item label="Page Size">
          <el-select v-model="form.pageSize">
            <el-option label="A4" value="A4" />
            <el-option label="Letter" value="Letter" />
            <el-option label="Legal" value="Legal" />
          </el-select>
        </el-form-item>

        <el-form-item label="Orientation">
          <el-select v-model="form.orientation">
            <el-option label="Portrait" value="portrait" />
            <el-option label="Landscape" value="landscape" />
          </el-select>
        </el-form-item>

        <el-form-item label="Margins">
          <el-input v-model="form.margins" placeholder="left,top,right,bottom (pt)" />
          <div class="form-tip">Default 36,36,36,36 (unit: pt)</div>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" :loading="loading" :disabled="!selectedFile" @click="handleSubmit">
            {{ loading ? 'Processing...' : 'Start Convert' }}
          </el-button>
          <el-button @click="resetForm">Reset</el-button>
        </el-form-item>
      </el-form>

      <div v-if="result" class="result-section">
        <el-divider>Processing Results</el-divider>
        <el-alert :title="result.msg" type="success" :closable="false" show-icon />
        <div class="result-info">
          <p><strong>Task ID:</strong> {{ result.data.taskId }}</p>
          <p><strong>Output File:</strong> {{ result.data.convertedFile }}</p>
          <el-button type="success" @click="downloadResult">
            <el-icon><Download /></el-icon>
            Download PDF
          </el-button>
        </div>
      </div>
    </el-card>

    <section class="info-block">
      <h2>Feature Description</h2>
      <p>Convert plain text (.txt) to PDF with customizable fonts, sizes, margins, and pagination strategies.</p>
      <h3>Use Cases</h3>
      <ul>
        <li>Turn notes or exported logs into PDFs for storage and sharing.</li>
        <li>Convert system-generated text reports to standardized PDFs for archiving.</li>
        <li>Export code snippets using monospaced fonts into PDFs.</li>
      </ul>
    </section>
  </div>
</template>

<script setup>
import { ref, reactive } from 'vue'
import { ElMessage } from 'element-plus'
import { Document, UploadFilled, Download } from '@element-plus/icons-vue'
import { pdfApi } from '@/api'
import { downloadFile } from '@/utils/download'

const uploadRef = ref()
const loading = ref(false)
const selectedFile = ref(null)
const result = ref(null)

const form = reactive({
  encoding: 'utf-8',
  pageSize: 'A4',
  orientation: 'portrait',
  margins: '36,36,36,36'
})

const handleFileChange = (file) => {
  const raw = file.raw
  const isTxt = /\.txt$/i.test(raw.name)
  if (!isTxt) {
    ElMessage.error('Only .txt files are supported')
    uploadRef.value.clearFiles()
    return
  }
  selectedFile.value = raw
}

const beforeUpload = (file) => {
  const isTxt = /\.txt$/i.test(file.name)
  if (!isTxt) {
    ElMessage.error('Only .txt files are supported')
    return false
  }
  return false
}

const handleSubmit = async () => {
  if (!selectedFile.value) {
    ElMessage.warning('Please select a TXT file first')
    return
  }
  loading.value = true
  try {
    const formData = new FormData()
    formData.append('file', selectedFile.value)
    formData.append('encoding', form.encoding)
    formData.append('pageSize', form.pageSize)
    formData.append('orientation', form.orientation)
    formData.append('margins', form.margins)

    const response = await pdfApi.fromTxt(formData)
    result.value = response
    ElMessage.success('Conversion successful')
  } catch (e) {
    console.error(e)
  } finally {
    loading.value = false
  }
}

const downloadResult = () => {
  if (result.value?.data?.downloadUrl) {
    try {
      const filename = result.value.data.convertedFile || 'converted.pdf'
      
      // 使用统一的下载工具函数
      downloadFile(result.value.data.downloadUrl, filename)
      
      ElMessage.success('Download started')
    } catch (error) {
      console.error('Download failed:', error)
      ElMessage.error('Download failed, please try again')
    }
  } else {
    ElMessage.warning('No file available for download')
  }
}

const resetForm = () => {
  form.encoding = 'utf-8'
  form.pageSize = 'A4'
  form.orientation = 'portrait'
  form.margins = '36,36,36,36'
  selectedFile.value = null
  result.value = null
  uploadRef.value?.clearFiles()
}
</script>

<style scoped>
.txt-to-pdf { max-width: 800px; margin: 0 auto; }
.card-header { display: flex; justify-content: space-between; align-items: center; font-size: 18px; font-weight: bold; }
.form-tip { font-size: 12px; color: #909399; margin-top: 5px; }
.result-section { margin-top: 20px; }
.result-info { margin-top: 15px; padding: 15px; background-color: #f5f7fa; border-radius: 4px; }

.info-block { margin: 16px auto 0; padding: 16px; border: 1px solid var(--el-card-border-color, #ebeef5); background-color: var(--el-fill-color-blank, #fff); border-radius: var(--el-border-radius-base, 4px); box-sizing: border-box; max-width: 800px; }
</style>
